<template>
  <main v-if="true">
    <customer-info :loading="false"/>
    <tab-header :current="currentTab"/>
    <div class="tab-content">
      <div class="container-fluid">
        <div class="tab-pane active show">
          <div class="form-group">
            <label><b>服務歷程(近三個月)：</b></label>
            <div class="radio radio-inline">
              <input type="radio" id="service-1" v-model="currentServiceTab" value="0">
              <label for="service-1">全部</label>
            </div>
            <div class="radio radio-inline">
              <input type="radio" id="service-2" v-model="currentServiceTab" value="1">
              <label for="service-2">0800諮詢</label>
            </div>
            <div class="radio radio-inline">
              <input type="radio" id="service-3" v-model="currentServiceTab" value="2">
              <label for="service-3">臨櫃諮詢</label>
            </div>
            <div class="radio radio-inline">
              <input type="radio" id="service-4" v-model="currentServiceTab" value="3">
              <label for="service-4">線上遊覽(官網、MML)</label>
            </div>
            <div class="radio radio-inline">
              <input type="radio" id="service-5" v-model="currentServiceTab" value="4">
              <label for="service-5">產險</label>
            </div>
          </div>
          <div class="insurance-row row" v-if="serviceShowList.length != 0">
            <table class="table insurance-green" >
              <thead>
                <tr>
                  <th>序號</th>
                  <!-- <th>時間</th> -->
                  <th>類別</th>
                  <th>服務契機</th>
                </tr>
              </thead>
              <tbody>
                <tr
                v-for="(item, i) in serviceShowList"
                :key="`service-history-${i}`">
                  <td>{{item.id}}</td>
                  <!-- <td>{{item.created_at}}</td> -->
                  <td>
                    {{item.typeMsg}}
                  </td>
                  <td>{{item.content}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-else class="text-center" colspan="6">
              <h3 class="mb-3"><center>查無資料</center></h3>
            </div>
        </div>
      </div>
    </div>

    <!-- Modal -->
  </main>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { Carousel, Slide } from 'vue-carousel';
import { commonMixin } from '../../mixins/common';
import { getServiceRecord } from '../../service/index';

import Avatar from '../../components/Avatar';
import CustomerInfo from '../components/CustomerDetail/CustomerInfo';
import TabHeader from '../components/CustomerDetail/TabHeader';

export default {
  name: 'CustomerDetail5',
  mixins: [commonMixin],
  data () {
    return {
      currentServiceTab: 0,
      currentTab: 0,
      serviceList: [],
      serviceShowList: []
    }
  },
  components: { 
    Avatar, CustomerInfo, TabHeader, // basic component
  },
  computed: {
    ...mapGetters(['BaseUrl'])
  },
  watch: {
    'currentServiceTab': 'filterServiceList'
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ])
  },
  methods: {
    filterServiceList(val) {
      if (val == 0) {
        this.serviceShowList = this.serviceList.map(item => item)
      } else {
        this.serviceShowList = this.serviceList.filter(item => item.type==val)
      }
    },
    initCustomerDetail5() {
      getServiceRecord(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result =>{
        if(result.body.detail) {
          this.serviceList = result.body.detail.map((item, index) => {
            return {
              id: index + 1,
              created_at: item.date || 'noData',
              type: item.type || 5,
              content: item.SERVICE_DETAIL || '',
              typeMsg: item.SERVICE_SOURCE || ''
            }
          })
          this.serviceShowList = this.serviceList.map(item => item)
        } else {
          this.serviceList = []
          this.serviceShowList = []
        }
      })
    }
  },
  beforeMount () {
    let routeNumber = (this.$route.name.split(/-/g)).reverse()
    this.currentTab = Number(routeNumber[0])
  },
  async mounted () {
    this.initCustomerDetail5()
  }
}
</script>
